#main_table {
  table-layout: fixed;
  width: 100%;
}
#main_table td {
  padding: 20px;
  text-align: center;
}
.dzial{
  font-size: 1.6em;
  color: black
}
.sekcja_dzial {
  font-size: 3em;
  color: black
}

.projekty, tr {
  border-collapse: collapse;
}

.projekty td {
  border: 1px solid black;
}
.wartosc {
  font-size: 3.2em;
  color: #ffffff;
}

.więcej {
  font-size: 0.8em;
  color: green;
}

.mniej {
  font-size: 0.8em;
  color: red;
}

#nazwa {
  text-align: center;
  font-size: 5em;
}

.chart {
  display: grid;
  grid-template-columns: 0.4fr 0.6fr;
  grid-gap: 20px;
}

.legend {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2px;
}